'use strict';

import Interfaces from './interfaces/';
import math from './util/math';
import Rack from './core/rack';
import Tune from './tuning/tuning';
import * as Transform from './util/transform';

let Counter = require('./models/counter');
let Radio = require('./models/radio');
let Drunk = require('./models/drunk');
let Sequence = require('./models/sequence');
let Matrix = require('./models/matrix');

import WAAClock from 'waaclock';
import Interval from './time/interval';

const DefaultContext = window.AudioContext || window.webkitAudioContext;

function unlockAudioContext(audioContext) {
  const events = ['touchstart', 'touchend', 'mousedown', 'keydown'];

  function unlock() {
    events.forEach((eventName) => document.body.removeEventListener(eventName, unlock));
    if (audioContext.state === 'suspended') {
      console.log('Audio context - unlocked ...');
      audioContext.resume();
    }
  }

  if (audioContext.state === 'suspended') {
    console.log('Audio context - suspended until user interaction ...');
    events.forEach(eventName => {
      document.body.addEventListener(eventName, unlock, false);
    });
  }
}

/**
NexusUI => created as Nexus
*/

class NexusUI {

  constructor(context) {

    for (let key in Interfaces) {
      this[key] = Interfaces[key];
    }

    for (let key in math) {
      this[key] = math[key];
    }

    let Core = {
      'Rack': Rack
    };

    let Models = {
      'Counter': Counter,
      'Radio': Radio,
      'Drunk': Drunk,
      'Sequence': Sequence,
      'Matrix': Matrix
    };

    for (let key in Models) {
      this[key] = Models[key];
    }

    for (let key in Core) {
      this[key] = Core[key];
    }

    this._context = context;

    this.tune = new Tune();
    this.note = this.tune.note.bind(this.tune);

    this._clock = null;
    this.Interval = Interval;

    this.colors = {
      accent: '#2bb',
      fill: '#eee',
      light: '#fff',
      dark: '#333',
      mediumLight: '#ccc',
      mediumDark: '#666'
    };

    this.transform = Transform;
    this.add = Transform.add;


    this.Add = {};
    for (let key in Interfaces) {
      this.Add[key] = Transform.add.bind(this, key);
    }




    /* create default component size */
    /* jshint ignore:start */
    var existingStylesheets = document.getElementsByTagName("style");
    var defaultSizeDeclaration = '[nexus-ui]{height:5000px;width:5000px}';
    var defaultStyleNode = document.createElement('style');
    defaultStyleNode.type = 'text/css';
    defaultStyleNode.innerHTML = defaultSizeDeclaration;
    if (existingStylesheets.length > 0) {
      var parent = existingStylesheets[0].parentNode
      parent.insertBefore(defaultStyleNode, existingStylesheets[0])
    } else {
      document.write('<style>' + defaultSizeDeclaration + '<\/style>');
    }
    /* jshint ignore:end */

  }

  get context() {
    if (!this._context) {
      this._context = new DefaultContext();
    }
    return this._context;
  }

  set context(ctx) {
    if (this._clock) {
      this._clock.stop();
      this._clock = null;
    }

    this._context = ctx;
    unlockAudioContext(ctx);
  }

  get clock() {
    if (!this._clock) {
      this._clock = new WAAClock(this.context);
      this._clock.start();
    }
    return this._clock;
  }
}

let Nexus = new NexusUI();

export function colors() {
    return Nexus.colors;
}
export function context() {
    return Nexus.context;
}
export function clock() {
    return Nexus.clock;
}

export default Nexus;



